.container {
  background-color: #f1f2f6;
}

/* For the slider from codepen io*/
input[type=range] {
    box-sizing: border-box;
    font-size: 16px;
    line-height: 1;
    height: 2em;
    cursor: pointer;
    -webkit-appearance: none;
    width: 80%;
    background-color: transparent;
  }

  input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
  }

  input[type=range]:focus {
    outline: none;
  }

  input[type=range]::-ms-track {
    width: 100%;
    cursor: pointer;
    background: transparent;
    border-color: transparent;
    color: transparent;
  }

  input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 2em;
    height: 2em;
    margin-top: 0;
    background-color: rgb(63, 99, 136);
    border-radius: 1em;
    border: 2px solid rgba(255, 255, 255, 0.5);
    cursor: pointer;
  }

  input[type=range]::-moz-range-thumb {
    width: 2em;
    height: 2em;
    margin-top: 0;
    background-color: rgb(63, 99, 136);
    border-radius: 1em;
    border: 2px solid rgba(255, 255, 255, 0.5);
    cursor: pointer;
  }

  input[type=range]:hover::-webkit-slider-thumb {
    border-color: rgba(255, 255, 255, 0.7);
  }

  input[type=range]:hover::-moz-range-thumb {
    border-color: rgba(255, 255, 255, 0.7);
  }

  input[type=range]:active::-webkit-slider-thumb {
    border-color: #ffffff;
  }

  input[type=range]:active::-moz-range-thumb {
    border-color: #ffffff;
  }

  input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    cursor: pointer;
    height: 1em;
    border-bottom: 2px solid rgba(0, 117, 235, 0.1);
    background-color: transparent;
  }

  input[type=range]::-moz-range-track {
    width: 100%;
    cursor: pointer;
    height: 1em;
    border-bottom: 2px solid rgba(0, 117, 235, 0.1);
    background-color: transparent;
  }
  


.inputContainer {
    display: flex;
    margin-top: 20px;
    margin-bottom: 50px;
    align-items: flex-end;
    justify-content: center;
}

.inputContainer span{
    margin: 0 15px;
    color: white;
    background-color: rgb(63, 99, 136);
    padding: 10px;
    color: white;
    width: 30px;
    border: 1px solid rgba(0, 117, 235, 0.7);
    border-radius: 10px;
    text-align: center;
}